import React from "react";
import "./AboutMini.css";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "animate.css/animate.min.css";
import ScrollAnimation from "react-animate-on-scroll";
import { Link } from "react-router-dom";

function AboutMini() {
  return (
    <div className="about-mini">
      <Row className="about-mini-firstRow">
        <Col md={5} xs={12} className="a-m-firstRow-col1-container">
          <ScrollAnimation
            animateIn="animate__fadeInLeft"
            animateOut="animate__fadeIn"
            duration={2}
          >
            <Col md={12} xs={12} className="a-m-firstRow-col1">
              <div className="about-us-title-decoration">
                <h1 className="about-us-title2">About us</h1>
              </div>
              <Row className="about-mini-secondRow">
                <button className="about-us-mini-button">
                  <Link to="about" className=" footer-Link">
                    Learn more
                  </Link>
                </button>
              </Row>
            </Col>
          </ScrollAnimation>
        </Col>

        <Col md={7} xs={12}>
          <ScrollAnimation
            animateIn="animate__fadeInRight"
            animateOut="animate__fadeIn"
            duration={2}
          >
            <Col md={12} xs={12} className="a-m-firstRow-col2">
              <Row className="a-m-firstRow-col2-row1">
                <Col md={4} xs={12} className="a-m-firstRow-col2-row1-col1">
                  <h5>Our Capabilities</h5>
                </Col>

                <Col md={8} xs={12} className="a-m-firstRow-col2-row1-col2">
                  <p>
                    We got a plenty of special capabilities
                    <br />
                    <span className="capabilities-span">Business Strategy</span>
                    <br />
                    <span className="capabilities-span">UX designing</span>
                    <br />
                    <span className="capabilities-span">Creative</span>
                    <br /> and other more
                  </p>
                </Col>
              </Row>
              <Row className="a-m-firstRow-col2-row2">
                <Col md={4} xs={4} className="a-m-firstRow-col2-row2-col1">
                  <h5>How we work</h5>
                </Col>
                <Col md={8} xs={12} className="a-m-firstRow-col2-row2-col2">
                  <p>
                    Small teams working on big challenges in honset
                    collaboration with our clients.
                  </p>
                  <p>
                    <span className="capabilities-span">Small Teams</span>
                    <span className="capabilities-span">
                      Client as a{" "}
                      <span className="howWeWork-partner-sapn">partner</span>.
                    </span>
                  </p>
                </Col>
              </Row>
              <Row className="a-m-firstRow-col2-row3">
                <Col md={4} xs={12} className="a-m-firstRow-col2-row3-col1">
                  <h5>Leadership</h5>
                </Col>
                <Col md={8} xs={12} className="a-m-firstRow-col2-row3-col2">
                  <span className="about-us-mini-leadership">
                    Mohamed Salah -
                    <span className="leadership-position"> CEO</span>
                  </span>
                  <br />
                  <span className="about-us-mini-leadership">
                    Ahmed Den
                    <span className="leadership-position"> - Presedent</span>
                  </span>
                  <br />
                  <span className="about-us-mini-leadership">
                    Mark darwin -{" "}
                    <span className="leadership-position">CFO</span>
                  </span>
                </Col>
              </Row>
            </Col>
          </ScrollAnimation>
        </Col>
      </Row>
    </div>
  );
}

export default AboutMini;
